<template>
  <div id="content">
    <h1>{{ post.title }}</h1>

    <!--<div v-html="compiledMarkdown"></div>-->

    <mavon-editor
      class="md"
      :value="post.body"
    :subfield = "prop.subfield"
    :defaultOpen = "prop.defaultOpen"
    :toolbarsFlag = "prop.toolbarsFlag"
    :editable="prop.editable"
    :scrollStyle="prop.scrollStyle"
    ></mavon-editor>

    <hr>

    <div v-if="this.$store.state.username">
      <!--<b-button variant="outline-primary" @click="reply">回复</b-button>-->

    </div>

  </div>
</template>

<script>
    export default {
        name: "Article",
      data() {
        return {
          post: {}
        }
      },

      methods:{
        getContent(){
          let that = this
          this.steem.api.getContent(this.$route.params.author, this.$route.params.permlink, function(err, result) {
            that.post = result
          })
        },

        reply(){

        }

      },

      computed: {
        prop () {
          let data = {
            subfield: false,
            defaultOpen: 'preview',
            editable: false,
            toolbarsFlag: false,
            scrollStyle: true
          }
          return data
        }
      },

      mounted() {
        this.getContent()

      }
    }
</script>

<style scoped>
  h1{
    font-size: 2rem;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid #ddd;
  }
  #content{
    color: rgba(55,53,58,0.75);
    align: center;
    margin: 1rem auto;
    width: 75%;
    padding-bottom: 3rem;
   }


  img {
    vertical-align: middle;
    width: 2rem !important;
    background-color: blueviolet;
  }
</style>



