import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/Login'
import Index from '../components/Index'
import Article from '../components/Article'
import Post from '../components/Post'
import Category from '../components/Category'
import Edit from '../components/Edit'
import Feed from '../components/Feed'
import Atriclecontent from "../components/Articlecontent"
import Hot from "../components/Hot"


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
      meta:{
        requireAuth:true
      }
    },
    {
      path: '/feed',
      name: 'feed',
      component: Feed
    },
    {
      path: '/articlecontent/@:author/:permlink',
      name: 'articlecontent',
      component: Atriclecontent
    },
    {
      path: '/hot',
      name: 'hot',
      component: Hot
    },
    {
      path: '/category',
      name: 'category',
      component: Category,
      meta:{
        requireAuth:true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/article/:permlink',
      name: 'article',
      component: Article
    },
    {
      path: '/post',
      name: 'post',
      component: Post,
      meta:{
        requireAuth:true
      }
    },
    {
      path: '/edit/:permlink',
      name: 'edit',
      component: Edit,
      meta:{
        requireAuth:true
      }
    },
  ]
})



