<template>
  <div id="content">
    <h1>{{ post.title }}</h1>

    <!--<div v-html="compiledMarkdown"></div>-->

    <mavon-editor
      class="md"
      :value="post.body"
    :subfield = "prop.subfield"
    :defaultOpen = "prop.defaultOpen"
    :toolbarsFlag = "prop.toolbarsFlag"
    :editable="prop.editable"
    :scrollStyle="prop.scrollStyle"
    ></mavon-editor>

    <hr>

    <div v-if="this.$store.state.username">
      <b-button variant="outline-primary" @click="edit(post.permlink)">编辑</b-button>
      <b-button variant="outline-primary" @click="del">删除</b-button>
    </div>

  </div>
</template>

<script>
    export default {
        name: "Article",
      data() {
        return {
          post: {},
          posts:[],
          db:{},
          key:''
        }
      },

      methods:{

        initDatabase() {
          let that = this
          let db
          let openRequest
          const dbName = that.$store.state.username
          const tableName = that.$store.state.username
          const indexedDB = window.indexedDB || window.webkitIndexedDB || window.mozIndexedDB || window.msIndexedDB
          if (!indexedDB) {
            alert("你的浏览器不支持IndexedDB")
          }
          return new Promise(resolve => {
            openRequest = indexedDB.open(dbName, 1) //创建或打开数据库

            //数据库创建或更新事件  创建或版本号升级时会触发，从而新建表
            openRequest.onupgradeneeded = function (event) {
              // console.log('数据库创建或更新')
              db = event.target.result
              // console.log(db.version, '数据库版本')
              if (!db.objectStoreNames.contains(tableName)) { //判断表是否存在
                let objectstore = db.createObjectStore(tableName, {keyPath: 'id', autoIncrement: true}) //指定主键 id, 自增
                //新建表索引，IDBObject.createIndex()的三个参数分别为索引名称、索引所在的属性、配置对象（说明该属性是否包含重复的值）。
                objectstore.createIndex('title', 'title', {unique: false})
              }
            }

            //数据库打开成功(如果数据库已经存在)
            openRequest.onsuccess = function (event) {
              db = event.target.result
              // console.log(777, db, '版本号', db.version)
              db.onerror = function (event) {
                //针对此数据库的所有错误的通用错误处理程序
                alert("Database error:" + event.target.errorCode)
                console.dir(event.target)
              }
              that.db = db
              // that.$store.commit('saveDB', db)
              resolve()
            }

            //数据库打开错误
            openRequest.onerror = function (event) {
              console.log(event, '数据库打开错误')
            }
          })
        },

        edit(permlink){
          this.$router.push({path: '/edit/'+permlink})
        },

        del(){
          let db = this.db
          let tableName = this.$store.state.username
          let transaction = db.transaction([tableName], 'readwrite')
          let objectStore = transaction.objectStore(tableName)
          objectStore.delete(this.key)
          db.close()
          this.$router.push('/')
        },

        searchKey() {
          let that = this
          let permlink = this.$route.params.permlink
          let db = this.db
          let tableName = this.$store.state.username
          let transaction = db.transaction([tableName], 'readwrite')
          let objectStore = transaction.objectStore(tableName)

          objectStore.openCursor().onsuccess = function (event) {  //使用游标遍历
            let cursor = event.target.result
            // console.log(cursor, '游标');
            if (cursor) {
              // console.log(11, cursor.key, cursor.value);
              if (cursor.value.permlink == permlink) {
                // console.log(666, '查到了666');
                that.key = cursor.key
                that.post = cursor.value
              }
              cursor.continue()
            }

            //如果游标遍历出现错误
            objectStore.openCursor().onerror = function (event) {
              console.dir(event)
            }
          }
        },
      },

      computed: {
        prop () {
          let data = {
            subfield: false,
            defaultOpen: 'preview',
            editable: false,
            toolbarsFlag: false,
            scrollStyle: true
          }
          return data
        }
      },

      mounted() {
        let _this = this
        async function main() {
          try {
             //初始化数据库
              await _this.initDatabase()
            //直接查找数据和 key 值
               _this.searchKey()
          }
          catch (err) {
            console.error(err);
          }
        }
        main()

      }
    }
</script>

<style scoped>
  h1{
    font-size: 2rem;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid #ddd;
  }
  #content{
    color: rgba(55,53,58,0.75);
    align: center;
    margin: 1rem auto;
    width: 75%;
    padding-bottom: 3rem;
   }


  img {
    vertical-align: middle;
    width: 2rem !important;
    background-color: blueviolet;
  }
</style>



