<template>
  <div class="article">
    <div class="content" v-for="post in posts">
      <router-link :to="'/articlecontent/'+'@'+post.author+'/'+post.permlink">{{ post.title }}　

        <p class="posttime">@{{ post.author }} 　{{ post.created  | formatTime}}</p>

      </router-link>
    </div>
  </div>
</template>

<script>
    export default {
        name: "Hot",
      data(){
          return{
            posts:[]
          }
      },
      methods:{
          getHot(){
            let that = this
            let query = { tag: "cn", limit : 50 }
            this.steem.api.getDiscussionsByTrending(query, function(err, data) {
              that.posts = data
            })
          }
      },
      mounted() {
          this.getHot()
      },

      filters:{
        formatTime(data){
          return data.substring(0,10)
        }
      },
    }
</script>

<style scoped>

  .article{
    margin-top: 3rem;
  }
  .posttime{
    font-size: 0.8rem;
    display: inline;
  }
  .content{
    color: rgba(55,53,58,0.75);
    align: center;
    margin: 1rem auto;
    height: 3rem;
    width: 70%;
    border-bottom: 1px solid #ddd;
  }
  img {
    vertical-align: middle;
    width: 2rem !important;
    background-color: blueviolet;
  }

  a{
    color: rgba(55,53,58,0.75);
  }

  a:hover{
    color: darkcyan;
    text-decoration: none;
  }

</style>



