<template>
  <div>

      <b-input
        id="inline-form-input-name"
        class="mb-2 mr-sm-2 mb-sm-0 search"
        placeholder="搜索" v-model="keywords"
      ></b-input>

     <div class="article">
        <div class="content" v-for="post in search(keywords)">
              <router-link :to="'/article/'+post.permlink"><p class="posttime">{{ post.created | formatTime }}</p> 　{{ post.title }}</router-link>
         </div>
     </div>

  </div>
</template>

<script>
    export default {
      name: "Index",
      data() {
        return {
          keywords: '',
          posts: [],
          db:{}
          }
      },
      methods: {
        initDatabase() {
          let that = this
          let db
          let openRequest
          const dbName = that.$store.state.username
          const tableName = that.$store.state.username
          const indexedDB = window.indexedDB || window.webkitIndexedDB || window.mozIndexedDB || window.msIndexedDB
          if (!indexedDB) {
            alert("你的浏览器不支持IndexedDB")
          }
          return new Promise(resolve => {
            openRequest = indexedDB.open(dbName, 1) //创建或打开数据库

            //数据库创建或更新事件  创建或版本号升级时会触发，从而新建表
            openRequest.onupgradeneeded = function (event) {
              // console.log('数据库创建或更新')
              db = event.target.result
              // console.log(db.version, '数据库版本')
              if (!db.objectStoreNames.contains(tableName)) { //判断表是否存在
                let objectstore = db.createObjectStore(tableName, {keyPath: 'id', autoIncrement: true}) //指定主键 id, 自增
                //新建表索引，IDBObject.createIndex()的三个参数分别为索引名称、索引所在的属性、配置对象（说明该属性是否包含重复的值）。
                objectstore.createIndex('title', 'title', {unique: false})
              }
            }

            //数据库打开成功(如果数据库已经存在)
            openRequest.onsuccess = function (event) {
              db = event.target.result
              // console.log(666, db, '版本号', db.version)
              db.onerror = function (event) {
                //针对此数据库的所有错误的通用错误处理程序
                alert("Database error:" + event.target.errorCode)
                console.dir(event.target)
              }
              that.db = db
              resolve()
            }

            //数据库打开错误
            openRequest.onerror = function (event) {
              console.log(event, '数据库打开错误')
            }
          })
        },

        savePosts() {
          let that = this
          let db = that.db
          let tableName = that.$store.state.username
          // console.log(888, db, '版本号', db.version)

          //得到操作 'testTable' 表的事务对象
          let transaction = db.transaction([tableName], 'readwrite') //可读可写事务

          transaction.oncomplete = function (event) {
            console.log("事务操作完成")
          }
          transaction.onerror = function (event) {
            console.log("事务操作出错")
            console.dir(event)
          }
          transaction.onsuccess = function (event) {
            console.log('事务操作成功')
          }

          //从事务对象里得到表里的objectStore对象
          let objectStore = transaction.objectStore(tableName)
          let posts = that.$store.state.posts
          for (let i = posts.length - 1; i > -1 ; i--) {
            objectStore.add(posts[i])
            // console.log(223, "添加成功")
          }

          //设置初始化标记
          let initFlag = that.$store.state.username
          localStorage.setItem(initFlag, 'steemjiang')
        },

        getData() {
          let that = this
          let db = that.db
          // console.log(656, that.db)
          let tableName = that.$store.state.username
          let transaction = db.transaction([tableName], 'readwrite')
          let objectStore = transaction.objectStore(tableName)

          objectStore.openCursor().onsuccess = function (event) {  //使用游标遍历
            let cursor = event.target.result
            // console.log(cursor, '游标');
            if (cursor) {
              // console.log(111, cursor.key, cursor.value);
              that.posts.push(cursor.value)
              cursor.continue()
            } else {
              // console.log('没有更多数据了！')
              // console.log(123, that.posts)
              that.$store.commit('savePosts', that.posts.reverse())
              db.close()
            }
          }

          //如果游标遍历出现错误
          objectStore.openCursor().onerror = function (event) {
            console.dir(event)
          }

        },

        search(keywords){
          let posts = this.$store.state.posts
          return posts.filter(item => {
            if(item.title.includes(keywords)){
              // console.log(166, '查到了！')
              return item
            }
          })
        },

      },

      filters:{
        formatTime(data){
          return data.substring(0,10)
        }
      },

      mounted() {
        let _this = this

        //获取steem链上作者的所有文章
        function getPosts(author, startPermlink, limit, posts) {
          return new Promise((resolve, reject) => {
            let beforeDate = new Date().toISOString().split('.')[0]
            _this.steem.api.getDiscussionsByAuthorBeforeDate(author, startPermlink, beforeDate, limit, function (err, result) {
              if (result.length > 1) {
                let newResult = []
                result.forEach(post => {
                  if (post.permlink != startPermlink) {
                    newResult.push(post)
                  }
                })
                posts = [...posts, ...newResult]
                getPosts(author, result[result.length - 1].permlink, limit, posts)
                  .then(resolve)
                  .catch(reject)
              } else {
                resolve(posts)
              }
            })
          })
        }

        async function main(author, startPermlink, limit, posts) {
          try {
            if(_this.$store.state.username){
              //初始化数据库
              await _this.initDatabase()

              //获取初始化标记（初始为null），第一次会获取链上数据并存入
              if (localStorage.getItem(_this.$store.state.username) == null) {
                let lastposts = await getPosts(author, startPermlink, limit, posts)
                _this.$store.commit('savePosts', lastposts)
                // console.log(699, lastposts)
                //存入数据库
                _this.savePosts()
              } else {
                //直接读取数据库
                _this.getData()
              }
            }

          }
          catch (err) {
            console.error(err);
          }
        }

        //传入要查询的作者名，初始标签，一次查询的条数，初始化数组
        main(this.$store.state.username, null, 100, [])

      },

      // beforeDestroy(){
      //   this.db.close()
      // },


 }

</script>

<style scoped>

  .article{
    margin-top: 3rem;
  }
  .posttime{
    font-size: 0.8rem;
    display: inline;
  }
  .content{
    color: rgba(55,53,58,0.75);
    align: center;
    margin: 1rem auto;
    height: 3rem;
    width: 70%;
    border-bottom: 1px solid #ddd;
  }
  img {
    vertical-align: middle;
    width: 2rem !important;
    background-color: blueviolet;
  }

  a{
    color: rgba(55,53,58,0.75);
  }

  a:hover{
    color: darkcyan;
    text-decoration: none;
  }

  .search{
    margin: 1rem auto !important;
    width: 75%;
  }
</style>



