<template>
  <div id="maintop">

    <b-input
      id="inline-form-input-name"
      class="mb-2 mr-sm-2 mb-sm-0 title"
      placeholder="请输入标题" v-model="title"
    ></b-input>

    <div id="main">
          <mavon-editor v-model="body" ref="md" @imgAdd="imgAdd" style="height: 100%"/>
    </div>

    <hr>
    <b-input
      id="inline-form-input-name"
      class="mb-2 mr-sm-2 mb-sm-0"
      placeholder="tags  (输入英文标签，以空格分隔)" v-model="tags"
    ></b-input>

    <div class="btn">
      <b-button variant="outline-primary" @click="post">提交</b-button>
    </div>

  </div>
</template>

<script>
    export default {
        name: "Post",
      data() {
        return {
          db:{},
          body: '',
          title: '',
          permlink: '',
          tags: '',
        }
      },

      methods: {
        initDatabase() {
          let that = this
          let db
          let openRequest
          const dbName = that.$store.state.username
          const tableName = that.$store.state.username
          const indexedDB = window.indexedDB || window.webkitIndexedDB || window.mozIndexedDB || window.msIndexedDB
          if (!indexedDB) {
            alert("你的浏览器不支持IndexedDB")
          }
          return new Promise(resolve => {
            openRequest = indexedDB.open(dbName, 1) //创建或打开数据库

            //数据库创建或更新事件  创建或版本号升级时会触发，从而新建表
            openRequest.onupgradeneeded = function (event) {
              console.log('数据库创建或更新')
              db = event.target.result
              console.log(db.version, '数据库版本')
              if (!db.objectStoreNames.contains(tableName)) { //判断表是否存在
                let objectstore = db.createObjectStore(tableName, {keyPath: 'id', autoIncrement: true}) //指定主键 id, 自增
                //新建表索引，IDBObject.createIndex()的三个参数分别为索引名称、索引所在的属性、配置对象（说明该属性是否包含重复的值）。
                objectstore.createIndex('title', 'title', {unique: false})
              }
            }

            //数据库打开成功(如果数据库已经存在)
            openRequest.onsuccess = function (event) {
              db = event.target.result
              // console.log(899, db, '版本号', db.version)
              db.onerror = function (event) {
                //针对此数据库的所有错误的通用错误处理程序
                alert("Database error:" + event.target.errorCode)
                console.dir(event.target)
              }
              //挂载到全局中
              that.db = db
              // that.$store.commit('saveDB', db)
              resolve()
            }

            //数据库打开错误
            openRequest.onerror = function (event) {
              console.log(event, '数据库打开错误')
            }
          })
        },

        post(){
            let wif = this.$store.state.password
            let parentAuthor = ''
            let parentPermlink = this.tags.trim().split(/\s+/)[0]
            let author = this.$store.state.username
            let permlink = this.permlink
            let title = this.title
            let body = this.body
            let tags = this.tags.trim().split(/\s+/)
            let jsonMetadata = {"tags": tags, "app": "steemit/1.0", "format": "markdown"}
            let that = this

            //发布到steem上
            this.steem.broadcast.comment(wif, parentAuthor, parentPermlink, author, permlink, title, body, jsonMetadata, function(err, result) {
              console.log(1234, err, result);
             })

            //发布到本地数据库
            //把数据拼成一个JSON
            let post = {
              "title": title,
              "author": author,
              "permlink": permlink,
              "body": body,
              "created": new Date().toISOString().split('.')[0],
              "json_metadata": JSON.stringify(jsonMetadata)
            }

            let db = this.db
            let tableName = this.$store.state.username
            console.log(856, db, '版本号', db.version)

            //得到操作 'testTable' 表的事务对象
            let transaction = db.transaction([tableName], 'readwrite') //可读可写事务
            transaction.oncomplete = function (event) {
              console.log("事务操作完成")
            }
            transaction.onerror = function (event) {
              console.log("事务操作出错")
              console.dir(event)
            }
            transaction.onsuccess = function (event) {
              console.log('事务操作成功')
            }

            //从事务对象里得到表里的objectStore对象
            let objectStore = transaction.objectStore(tableName)
            objectStore.add(post)

            db.close()

            //发布完后跳转
            this.$router.push('/')

          },

        imgAdd(pos, $file){
          // 第一步.将图片上传到服务器.
          var formdata = new FormData();
          formdata.append('smfile', $file);
          this.axios({
            url: 'https://sm.ms/api/v2/upload',
            method: 'post',
            data: formdata,
            headers: { 'Content-Type': 'multipart/form-data' },
          }).then((url) => {
            // 第二步.将返回的url替换到文本原位置![...](0) -> ![...](url)
            // $vm.$img2Url 详情见本页末尾
            // $vm.$img2Url(pos, url);
            console.log(899, url)
            this.$refs.md.$img2Url(pos, url.data.data.url)
          })
        },

        getStr(){
          function randomString(length, chars) {
            let result = ''
            for (var i = length; i > 0; --i) result += chars[Math.floor(Math.random() * chars.length)]
            return result;
          }
          this.permlink = randomString(5, '0123456789abcdefghijklmnopqrstuvwxyz')
        }

      },

      mounted() {
        this.initDatabase()
        this.getStr()
        this.getDate()
      }
    }
</script>

<style scoped>
  #maintop{
    width: 90%;
    height: 100%;
    margin: 0 auto;
  }

  #main{
    height: 40rem;
  }
  .title{
    margin-top: 1.5rem;
    margin-bottom: 1.5rem !important;
    width: 60%;
  }
  .btn{
    margin-top: 0.3rem;
  }
</style>



